export class Model {
  user;
  items;

  constructor() {
    this.user = `Denis`;
    this.items = [
      new TodoItem(`Купить биткоины у цыган`, false),
      new TodoItem(`Познать дзен и Angular`, false),
      new TodoItem(`Украсть лошадь`, false),
      new TodoItem(`Поменять струны`, false)
    ]
  }
}

export class TodoItem {
  action;
  done;

  constructor(action, done) {
      this.action = action;
      this.done = done;
  }
}


